import React, { lazy, Component } from 'react'
import {
    CBadge,
    CButton,
    CButtonGroup,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CProgress,
    CRow,
    CCallout
} from '@coreui/react'

import CIcon from '@coreui/icons-react'
import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';

import './New.css'

import grapesjs from 'grapesjs';
import { Editor } from 'grapesjs-react';

import Axios from '../../lib/api';


class New extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: ''
        }
        console.log(props)
        this.pageId = props.match.params._id;
        console.log(this.pageId)
    }
    componentDidMount = () => {
        this.editor = grapesjs.init({
            // Indicate where to init the editor. You can also pass an HTMLElement
            container: '#gjs',
            // Get the content for the canvas directly from the element
            // As an alternative we could use: `components: '<h1>Hello World Component!</h1>'`,
            fromElement: true,
            // Size of the editor
            // height: '300px',
            width: 'auto',
            noticeOnUnload: false,
            storageManager: {
                id: 'gjs-',             // Prefix identifier that will be used inside storing and loading
                type: 'local',          // Type of the storage
                autosave: false,         // Store data automatically
                autoload: false,         // Autoload stored data on init
                stepsBeforeSave: 0,     // If autosave enabled, indicates how many changes are necessary before store method is triggered
                storeComponents: true,  // Enable/Disable storing of components in JSON format
                storeStyles: false,      // Enable/Disable storing of rules in JSON format
                storeHtml: false,        // Enable/Disable storing of components as HTML string
                storeCss: false,         // Enable/Disable storing of rules as CSS string
            },
            assetManager: {
                // storageType: '',
                // storeOnChange: true,
                // storeAfterUpload: true,
                upload: `${process.env.REACT_APP_BASE_URL}storage/upload`,        //for temporary storage
                // assets: [],
                // uploadName: 'file',
                // multiUpload: true,
                uploadFile: (e) => {
                    var files = e.dataTransfer ? e.dataTransfer.files : e.target.files;
                    console.log(files)
                    var formData = new FormData();
                    formData.append("file", files[0]);
                    Axios.post(`${process.env.REACT_APP_BASE_URL}storage/upload`, formData, {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    }).then(r => {
                        console.log(r)
                        const am = this.editor.AssetManager;
                        am.add(r.data);
                    })
                        .catch(e => {
                            console.error(e)
                        })
                }
            },
            // Avoid any default panel
            panels: {
                defaults: [
                    // ...
                    {
                        id: 'panel-devices',
                        el: '.panel__devices',
                        buttons: [{
                            id: 'device-desktop',
                            // label: 'D',
                            command: 'set-device-desktop',
                            active: true,
                            togglable: false,
                        }, {
                            id: 'device-mobile',
                            // label: 'M',
                            command: 'set-device-mobile',
                            togglable: false,
                        }],
                    }]
            },
            mediaCondition: 'min-width', // default is `max-width`
            deviceManager: {
                devices: [{
                    name: 'Mobile',
                    width: '320',
                    widthMedia: '',
                }, {
                    name: 'Desktop',
                    width: '',
                    widthMedia: '1024',
                }]
            },
            plugins: [
                // 'grapesjs-lory-slider',
                // 'grapesjs-tabs',
                // 'grapesjs-custom-code',
                // 'pesjs-plugin-forms',
                // 'grapesjs-touch',
                // 'grapesjs-parser-postcss',
                // 'grapesjs-tooltip',
                // 'grapesjs-tui-image-editor',
                // 'grapesjs-typed',
                // 'grapesjs-style-bg',
                'grapesjs-parser-postcss',
                'gjs-preset-newsletter',
                'gjs-preset-webpage',
                // 'gjs-plugin-filestack'
            ],
            pluginsOpts: {
                'gjs-preset-newsletter': {

                },
                'grapesjs-lory-slider': {
                    sliderBlock: {
                        category: 'Extra'
                    }
                },
                'grapesjs-tabs': {
                    tabsBlock: {
                        category: 'Extra'
                    }
                },
                'grapesjs-typed': {
                    block: {
                        category: 'Extra',
                        content: {
                            type: 'typed',
                            'type-speed': 40,
                            strings: [
                                'Text row one',
                                'Text row two',
                                'Text row three',
                            ],
                        }
                    }
                },
                'gjs-preset-webpage': {
                    modalImportTitle: 'Import Template',
                    modalImportLabel: '<div style="margin-bottom: 10px; font-size: 13px;">Paste here your HTML/CSS and click Import</div>',
                    modalImportContent: function (editor) {
                        return editor.getHtml() + '<style>' + editor.getCss() + '</style>'
                    },
                    filestackOpts: null, //{ key: 'AYmqZc2e8RLGLE7TGkX3Hz' },
                    aviaryOpts: false,
                    blocksBasicOpts: { flexGrid: 1 },
                    customStyleManager: [{
                        name: 'General',
                        buildProps: ['float', 'display', 'position', 'top', 'right', 'left', 'bottom'],
                        properties: [{
                            name: 'Alignment',
                            property: 'float',
                            type: 'radio',
                            defaults: 'none',
                            list: [
                                { value: 'none', className: 'fa fa-times' },
                                { value: 'left', className: 'fa fa-align-left' },
                                { value: 'right', className: 'fa fa-align-right' }
                            ],
                        },
                        { property: 'position', type: 'select' }
                        ],
                    }, {
                        name: 'Dimension',
                        open: false,
                        buildProps: ['width', 'flex-width', 'height', 'max-width', 'min-height', 'margin', 'padding'],
                        properties: [{
                            id: 'flex-width',
                            type: 'integer',
                            name: 'Width',
                            units: ['px', '%'],
                            property: 'flex-basis',
                            toRequire: 1,
                        }, {
                            property: 'margin',
                            properties: [
                                { name: 'Top', property: 'margin-top' },
                                { name: 'Right', property: 'margin-right' },
                                { name: 'Bottom', property: 'margin-bottom' },
                                { name: 'Left', property: 'margin-left' }
                            ],
                        }, {
                            property: 'padding',
                            properties: [
                                { name: 'Top', property: 'padding-top' },
                                { name: 'Right', property: 'padding-right' },
                                { name: 'Bottom', property: 'padding-bottom' },
                                { name: 'Left', property: 'padding-left' }
                            ],
                        }],
                    }, {
                        name: 'Typography',
                        open: false,
                        buildProps: ['font-family', 'font-size', 'font-weight', 'letter-spacing', 'color', 'line-height', 'text-align', 'text-decoration', 'text-shadow'],
                        properties: [
                            { name: 'Font', property: 'font-family' },
                            { name: 'Weight', property: 'font-weight' },
                            { name: 'Font color', property: 'color' },
                            {
                                property: 'text-align',
                                type: 'radio',
                                defaults: 'left',
                                list: [
                                    { value: 'left', name: 'Left', className: 'fa fa-align-left' },
                                    { value: 'center', name: 'Center', className: 'fa fa-align-center' },
                                    { value: 'right', name: 'Right', className: 'fa fa-align-right' },
                                    { value: 'justify', name: 'Justify', className: 'fa fa-align-justify' }
                                ],
                            }, {
                                property: 'text-decoration',
                                type: 'radio',
                                defaults: 'none',
                                list: [
                                    { value: 'none', name: 'None', className: 'fa fa-times' },
                                    { value: 'underline', name: 'underline', className: 'fa fa-underline' },
                                    { value: 'line-through', name: 'Line-through', className: 'fa fa-strikethrough' }
                                ],
                            }, {
                                property: 'text-shadow',
                                properties: [
                                    { name: 'X position', property: 'text-shadow-h' },
                                    { name: 'Y position', property: 'text-shadow-v' },
                                    { name: 'Blur', property: 'text-shadow-blur' },
                                    { name: 'Color', property: 'text-shadow-color' }
                                ],
                            }],
                    }, {
                        name: 'Decorations',
                        open: false,
                        buildProps: ['opacity', 'border-radius', 'border', 'box-shadow', 'background-bg'],
                        properties: [{
                            type: 'slider',
                            property: 'opacity',
                            defaults: 1,
                            step: 0.01,
                            max: 1,
                            min: 0,
                        }, {
                            property: 'border-radius',
                            properties: [
                                { name: 'Top', property: 'border-top-left-radius' },
                                { name: 'Right', property: 'border-top-right-radius' },
                                { name: 'Bottom', property: 'border-bottom-left-radius' },
                                { name: 'Left', property: 'border-bottom-right-radius' }
                            ],
                        }, {
                            property: 'box-shadow',
                            properties: [
                                { name: 'X position', property: 'box-shadow-h' },
                                { name: 'Y position', property: 'box-shadow-v' },
                                { name: 'Blur', property: 'box-shadow-blur' },
                                { name: 'Spread', property: 'box-shadow-spread' },
                                { name: 'Color', property: 'box-shadow-color' },
                                { name: 'Shadow type', property: 'box-shadow-type' }
                            ],
                        }, {
                            id: 'background-bg',
                            property: 'background',
                            type: 'bg',
                        },],
                    }, {
                        name: 'Extra',
                        open: false,
                        buildProps: ['transition', 'perspective', 'transform'],
                        properties: [{
                            property: 'transition',
                            properties: [
                                { name: 'Property', property: 'transition-property' },
                                { name: 'Duration', property: 'transition-duration' },
                                { name: 'Easing', property: 'transition-timing-function' }
                            ],
                        }, {
                            property: 'transform',
                            properties: [
                                { name: 'Rotate X', property: 'transform-rotate-x' },
                                { name: 'Rotate Y', property: 'transform-rotate-y' },
                                { name: 'Rotate Z', property: 'transform-rotate-z' },
                                { name: 'Scale X', property: 'transform-scale-x' },
                                { name: 'Scale Y', property: 'transform-scale-y' },
                                { name: 'Scale Z', property: 'transform-scale-z' }
                            ],
                        }]
                    }, {
                        name: 'Flex',
                        open: false,
                        properties: [{
                            name: 'Flex Container',
                            property: 'display',
                            type: 'select',
                            defaults: 'block',
                            list: [
                                { value: 'block', name: 'Disable' },
                                { value: 'flex', name: 'Enable' }
                            ],
                        }, {
                            name: 'Flex Parent',
                            property: 'label-parent-flex',
                            type: 'integer',
                        }, {
                            name: 'Direction',
                            property: 'flex-direction',
                            type: 'radio',
                            defaults: 'row',
                            list: [{
                                value: 'row',
                                name: 'Row',
                                className: 'icons-flex icon-dir-row',
                                title: 'Row',
                            }, {
                                value: 'row-reverse',
                                name: 'Row reverse',
                                className: 'icons-flex icon-dir-row-rev',
                                title: 'Row reverse',
                            }, {
                                value: 'column',
                                name: 'Column',
                                title: 'Column',
                                className: 'icons-flex icon-dir-col',
                            }, {
                                value: 'column-reverse',
                                name: 'Column reverse',
                                title: 'Column reverse',
                                className: 'icons-flex icon-dir-col-rev',
                            }],
                        }, {
                            name: 'Justify',
                            property: 'justify-content',
                            type: 'radio',
                            defaults: 'flex-start',
                            list: [{
                                value: 'flex-start',
                                className: 'icons-flex icon-just-start',
                                title: 'Start',
                            }, {
                                value: 'flex-end',
                                title: 'End',
                                className: 'icons-flex icon-just-end',
                            }, {
                                value: 'space-between',
                                title: 'Space between',
                                className: 'icons-flex icon-just-sp-bet',
                            }, {
                                value: 'space-around',
                                title: 'Space around',
                                className: 'icons-flex icon-just-sp-ar',
                            }, {
                                value: 'center',
                                title: 'Center',
                                className: 'icons-flex icon-just-sp-cent',
                            }],
                        }, {
                            name: 'Align',
                            property: 'align-items',
                            type: 'radio',
                            defaults: 'center',
                            list: [{
                                value: 'flex-start',
                                title: 'Start',
                                className: 'icons-flex icon-al-start',
                            }, {
                                value: 'flex-end',
                                title: 'End',
                                className: 'icons-flex icon-al-end',
                            }, {
                                value: 'stretch',
                                title: 'Stretch',
                                className: 'icons-flex icon-al-str',
                            }, {
                                value: 'center',
                                title: 'Center',
                                className: 'icons-flex icon-al-center',
                            }],
                        }, {
                            name: 'Flex Children',
                            property: 'label-parent-flex',
                            type: 'integer',
                        }, {
                            name: 'Order',
                            property: 'order',
                            type: 'integer',
                            defaults: 0,
                            min: 0
                        }, {
                            name: 'Flex',
                            property: 'flex',
                            type: 'composite',
                            properties: [{
                                name: 'Grow',
                                property: 'flex-grow',
                                type: 'integer',
                                defaults: 0,
                                min: 0
                            }, {
                                name: 'Shrink',
                                property: 'flex-shrink',
                                type: 'integer',
                                defaults: 0,
                                min: 0
                            }, {
                                name: 'Basis',
                                property: 'flex-basis',
                                type: 'integer',
                                units: ['px', '%', ''],
                                unit: '',
                                defaults: 'auto',
                            }],
                        }, {
                            name: 'Align',
                            property: 'align-self',
                            type: 'radio',
                            defaults: 'auto',
                            list: [{
                                value: 'auto',
                                name: 'Auto',
                            }, {
                                value: 'flex-start',
                                title: 'Start',
                                className: 'icons-flex icon-al-start',
                            }, {
                                value: 'flex-end',
                                title: 'End',
                                className: 'icons-flex icon-al-end',
                            }, {
                                value: 'stretch',
                                title: 'Stretch',
                                className: 'icons-flex icon-al-str',
                            }, {
                                value: 'center',
                                title: 'Center',
                                className: 'icons-flex icon-al-center',
                            }],
                        }]
                    }
                    ],
                }
            }
        });

        // Commands
        this.editor.Commands.add('set-device-desktop', {
            run: editor => editor.setDevice('Desktop')
        });
        this.editor.Commands.add('set-device-mobile', {
            run: editor => editor.setDevice('Mobile')
        });
        this.editor.Commands.add('set-device-tablet', {
            run: editor => editor.setDevice('Tablet')
        });
        // Do something on response
        this.editor.on('asset:upload:response', (response) => {
            console.log(response)
            const am = this.editor.AssetManager;
            am.add(response);
        });
        if (this.pageId)
            Axios.get(`admin/page/pages?id=${this.pageId}`).then(r => {
                console.log(this.editor)
                this.setState({ page: r.data[0], title: r.data[0].title })
                this.editor.setComponents(r.data[0].html)
                this.editor.setStyle(r.data[0].css)
            })
    }
    onSave() {
        var { title, page } = this.state;
        Axios.post('admin/page/add-page', { id: page ? page.id : null, html: this.editor.runCommand('gjs-get-inlined-html'), css: this.editor.getCss(), title: title })
            .then(r => {
                console.log(r)
                this.props.history.push('/dashboard');
            })
            .catch(e => console.error(e))


    }
    onTitleChange(e) {
        this.setState({ title: e.target.value })
    }
    render() {

        return (
            <>
                <CRow>
                    <CCol>
                        {/* <CCard> */}
                        <CCardHeader>
                            Add New Page

                            </CCardHeader>
                        <CCardBody>
                            <input type="text" value={this.state.title} className="input" placeholder="Title " onChange={(e) => this.onTitleChange(e)} />
                            <CButton type="submit" size="sm" color="primary" onClick={() => { this.onSave() }}><CIcon name="cil-scrubber" /> Save</CButton>

                            {/* <Editor /> */}
                            <div id="gjs"></div>

                        </CCardBody>
                        {/* </CCard> */}
                    </CCol>
                </CRow>
            </>
        )
    }
}

export default New
